.sc-banner-home{
  // /////////////////////////////position: absolute;
  position: relative;
  margin-top: 28px;

  .bx-slider-home-row{
    position: relative;
  }

  li{
    .item{
      position: relative;
      padding-bottom: 35%;
      .dados{
        position: absolute;
        display: block;
        font-family: $font-quicksandbold;
        font-size: 3.3em;
        color: $branco;
        left: 0;
        right: 0;
        top: 0;
        bottom: 15px;
        @include respond-to(x-medium)
        {
          font-size: 2.5em;
        }

        @include respond-to(small)
        {
          display: none;
        }

        .texto{
          position: absolute;
          width: 600px;
          padding-left: 30px;
          padding-right: 30px;
          text-shadow: 2px 2px 3px rgba(0,0,0,0.4);

          @include respond-to(menu-mobile-home)
          {
            width: 100%;
          }
           // ALINHAMENTO TEXTO
          &.align-L{text-align: left};
          &.align-C{text-align: center};
          &.align-R{text-align: right};


          //POSICAO TEXTO
          &.pos-TL{top:0; left:0; margin-right: 400px;};
          &.pos-TC{top:0; left:0; margin-left: 200px; margin-right: 200px;};
          &.pos-TR{top:0; right:0; margin-left: 400px};

          &.pos-CL{top:50%; transform: translateY(-50%); left:0; margin-right: 400px;};
          &.pos-CC{top:50%; transform: translateY(-50%); margin-left: 200px; margin-right: 200px;};
          &.pos-CR{top:50%; transform: translateY(-50%); right:0; margin-left: 400px};

          &.pos-BL{bottom:0; left:0; margin-right: 400px;};
          &.pos-BC{bottom:0; left:0; margin-left: 200px; margin-right: 200px;};
          &.pos-BR{bottom:0; right:0; margin-left: 400px};

        }


      }

    }
  }

  .bx-wrapper .bx-viewport {
      @extend %slide-reset;
      max-width: none !important;
  }
  .bx-viewport{
      //height: auto !important;
      //height: 100% !important;
  }
  .bx-wrapper{
    margin: 0 auto 0;
    //height: 100% !important;
  }


  .bx-wrapper .bx-controls-direction{
      width: 100%;
      height: 100%;
      margin: 0 auto;
      position: absolute;
      top: 0;
      bottom: 0;
      display: none;

  }

  .bx-slider-home-row{
    position: relative;
  }

  .bx-next, .bx-prev{
    width: 58px !important;
    height: 103px !important;
    display: block;
    @include respond-to(small)
    {
      width: 40px !important;
      height: 71px !important;
    }
  }

  #slider-next {
    z-index: 20;
    opacity: 0;
    position: absolute;
    right: 70px;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    width: 58px;
    height: 103px;
    background: url(../imagens/img/bgs/seta-banner-next.png) no-repeat; // falback
    transition: background, 0s;

    @include respond-to(largura-site-sem-pad)
    {
      right: 50px;
    }

    @include respond-to(medium)
    {
      width: 40px;
      height: 71px;
      background-size: 40px 142px;
    }

    &:hover{
        background-position: 0 -103px !important;
        @include respond-to(medium)
        {
          background-position: 0 -71px !important;
        }
    }

  }

  #slider-prev {
    z-index: 20;
    opacity: 0;
    position: absolute;
    left: 70px;
    top: 50%;
    transform:translateY(-50%);
    display: block;
    width: 58px;
    height: 103px;
    background: url(../imagens/img/bgs/seta-banner-prev.png) no-repeat; // falback
    transition: opacity 0.3s, background, 0s;
    @include respond-to(largura-site-sem-pad)
    {
      left: 50px;
    }

    @include respond-to(medium)
    {
      width: 40px;
      height: 71px;
      background-size: 40px 142px;
    }
    &:hover{
        background-position: 0 -103px !important;
        @include respond-to(medium)
        {
          background-position: 0 -71px !important;
        }
    }
  }

  .showSetasSlider{
    opacity: 1 !important;
  }

}
