.pg-historia{
  .sc-historia{
    text-align: right;

    padding-top: 50px;
    padding-bottom: 250px;
    position: relative;
    //@include bgFoto("../img/bgs/bg_historia.jpg", top left, 100%);
    background-color: #13506c;
    background-image: url(../img/bgs/marca_daqua_historia.png), url(../img/bgs/bg_historia.jpg);
    background-size: auto, cover;
    background-position: left 30% top 65px, bottom left;
    background-repeat: no-repeat;

    @include respond-to(large2)
    {
      padding-bottom: 100px;
      background-image: url(../img/bgs/marca_daqua_historia.png), url(../img/bgs/bg_historia2.jpg);
      background-size: 40%, cover;
      background-position: left 10% top 40px, center;
    }

    @include respond-to(small)
    {
      padding-bottom: 50px;
    }

    .content{
      position: relative;
      padding-left: 52%;
      text-align: justify;

      @include respond-to(large2)
      {
        padding-left: 0;
      }

      h1{
        text-align: right;
      }
      h2{
        margin-top: 27px;
        margin-bottom: 27px;
      }
      p{
        font-size: 1.7em;
        margin-bottom: 30px;
      }
    }


  } //sc-historia
}
