//Susy setup ------------------------------------
$susy: (
	output: float,
	container: 1280px,
	math: fluid,
	columns: 12,
	column-width: 85px,
	gutters: 35px/85px,
	global-box-sizing: border-box,
	last-flow: to,
	debug: (image: false,
		color: rgba(#66f, 0.25),
		output: background,
		toggle: top right)
	);

.row{
	@include container();
	padding-left: 40px;
	padding-right: 40px;
	margin: 0 auto;

	@include respond-to(medium)
	{
		padding-left: 30px;
		padding-right: 30px;
	}

	@include respond-to(small)
	{
		padding-left: 15px;
		padding-right: 15px;
	}
} // row;
