.pg-contato{
  .sc-contato{
    .content{
      .container{
        .endereco{
          width: 30%;
          float: left;
          @include respond-to(largura-site-sem-pad)
          {
            width: 100%;
            float: none;
            margin-bottom: 30px;
          }
        }

        .formulario{
          width: 61%;
          float: right;
          @include respond-to(largura-site-sem-pad)
          {
            width: 100%;
            float: none;
            margin-bottom: 70px;
          }
        }

      }
    }
  }// sc-contato
}//pg-contato
