$breakpoints: (
  'largura-site'         : ( max-width:  1190px ),
  'largura-site-sem-pad' : ( max-width:  1090px ),
  'menu-mobile-home-min' : ( min-width:  801px ),
  'menu-mobile-home'     : ( max-width:  800px ),
  'large2'                : ( max-width:  1000px ),
  'ipad'                : ( max-width:  1024px ),
  'large'                : ( max-width:  950px ),
  'large3'               : ( max-width:  900px ),
  'medium'               : ( max-width:  800px ),
  'x-medium'             : ( max-width:  650px ),
  'small'                : ( max-width:  500px ),
  'small2'                : ( max-width:  550px ),
  'x-small'              : ( max-width:  400px ),
  'x-small2'             : ( max-width:  350px ),
  'h1800'                : ( max-width:  1800px ),
  'h1200'                : ( max-width:  1200px ),
  'h1070'                : ( max-width:  1070px ),
  'h1000'                : ( max-width:  1000px ),
  'h600'                 : ( max-width:  600px ),
  's1500'                : ( max-width:  1500px ),
  's1100'                : ( max-width:  1100px ),
  'hist1'                : (( max-width:  1380px )),
  'hist2'                : (( max-width:  1200px )),
  'c1400'                : (( max-width:  1400px )),
  's1300'                : (( max-width:  1300px )),
  's850'                 : (( max-width:  850px ))

);


@mixin respond-to($name) {
  // If the key exists in the map
  @if map-has-key($breakpoints, $name) {
    // Prints a media query based on the value
    @media #{inspect(map-get($breakpoints, $name))} {
      @content;
    }
  }

  // If the key doesn't exist in the map
  @else {
    @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
        + "Please make sure it is defined in `$breakpoints` map.";
  }
}
