.pg-home{
  .sc-galeria{
    margin-top: 50px;
    margin-bottom: 58px;

    h1{
      font-size: 3em;
      color: $vinho;
      margin-bottom: 8px;
    }
    p{
      color: $azul-noite;
    }

    .menu{
      padding-top: 14px;
      @include respond-to(menu-mobile-home)
      {
        @include full;
        margin-top: 40px;
      }
      .item{
        padding-top: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid shade($branco, 30%);
        transition: background-color 0.3s;

        @include respond-to(menu-mobile-home)
        {
          min-height: 130px;
        }

        &:hover{
          background-color: shade($branco, 3%);
        }

        .ft{
          width: 125px;
          height: 85px;
          float: left;
          margin-right: 14px;
        }
        p strong{
          color: $vinho;
        }
      } //item

      a:nth-child(1){
        .item{
          border-top: 2px solid $azul-noite;
        }
      }
      a.is-active{
        background-color: shade($branco, 5%);
      }


      .canalYoutube{
        font-family: $font-quicksandbold;
        display: block;
        margin-top: 20px;
        text-align: center;
        transition: color 0.3s;
        &:hover{
          color: $vinho;
        }
      }
    }//menu

    .video{
      div{
        position: relative;
        width: 100%;
        padding-bottom: 56.25%;

        &:before{
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: -50px;
          background-image: url(../imagens/img/bgs/tv_frame.jpg);
          background-repeat: no-repeat;
          background-size: 100%;
          display: block;
        }



      }
      iframe{
        position: absolute;
        top: 3px;
        left: 3px;
        width: calc(100% - 4px);
        height: 100%;
      }
    }


  }//sc-galeria
}// pg-home

.vertical-tabs-container {
  $medium-screen: 30em !default;
  $vertical-tabs-height: 20em;

  @include clearfix;

  .vertical-tabs {
    //display: none;

    @include media($medium-screen) {
      display: inline;
      float: left;
      width: 20%;
    }
  }

  .vertical-tab {
    @include media($medium-screen) {
      display: block;

      &.is-active {
        color: #ff0000;
      }
    }
  }

  .vertical-tab-content-container {
    display: block;

    @include media($medium-screen) {
      //@include size(80% $vertical-tabs-height);
      display: inline-block;
    }
  }

  .vertical-tab-content {
    @include media($medium-screen) {
      //display: none;
    }
  }

  .vertical-tab-accordion-heading {
    cursor: pointer;
    display: block;

    @include media($medium-screen) {
//      display: none;
    }
  }
}

