.sc-topo
{
  position: relative;
  border-top: none;
  margin-top: 0;
  padding-top: 30px;
  top: 0;

  .content{
    left: 0;
    right: 0;
    top: 30px;
  }

  .faixa-topo{
    text-align: center;
    position: relative;
    width: 100%;
    background-color: $azul-noite;

    display: inline-block;
    vertical-align: middle;
    height: 150px;

    @include respond-to(largura-site-sem-pad)
    {
      width: 100%;
//      padding-right: 50px;
      border-radius: 0;
    }

    @include respond-to(small)
    {
      height: 116px;
      // padding-right: 35px;
      // padding-left: 35px;
    }

    @include respond-to(x-small)
    {
      height: 103px;
    }

    .row.mnu{
      display: inline-block;
      vertical-align: middle;
      width: calc(100% - 225px);
      padding-left: 0;
      padding-right: 0;

      @include respond-to(menu-mobile-home)
      {
        width: 100%;
      }


    }

    .logo{
      display: inline-block;
      vertical-align: middle;
      width: 220px;
      svg{
        width: 220px;
      }

      @include respond-to(menu-mobile-home)
      {
        display: block;
      }

      @include respond-to(small)
      {
        width: 180px;
        height: 76px;
        margin-top: 20px;
        svg{
          width: 180px;
          height: 76px;
        }
      }

      @include respond-to(x-small)
      {
        width: 150px;
        height: 63px;
        margin-top: 20px;
        svg{
          width: 150px;
          height: 63px;
        }
      }

    }

  }
  .linhaTopo{
    position: relative;
    z-index: 10;
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    color: $azul-noite;
    margin-bottom: 23px;
    vertical-align: middle;

    @include respond-to(small)
    {
      padding-left: 35px;
      padding-right: 35px;
    }

    p{
      color: $azul-noite;
    }
    .venda{
      float: left;
      @include respond-to(x-medium)
      {
        display: none;
      }
    }

    .social{
      vertical-align: middle;
      float: right;
      p{
        display: inline-block;
        vertical-align: middle;
        @include respond-to(large)
        {
          display: none;
        }
      }
      svg{
        display: inline-block;
        vertical-align: middle;
        width: 25px;
        height: 25px;
        margin-left: 15px;
        fill: $vinho;
        transition: fill 0.3s;

        &:hover{
          fill: $azul-noite;
        }
      }
    }
  }

}//sc-topo
