.pg-contato{

  .sc-contato
  {
    background-image: url(../img/bgs/bg_contato.jpg);
    background-size: calc(100% + 300px);
    background-color: #7c9ad0;
    background-repeat: no-repeat;
    background-position: bottom center;
    padding-top: 62px;
    padding-bottom: 380px;

    @include respond-to(medium)
    {
      padding-bottom: 250px;
    }

    .content{
      position: relative;
    }

    .respFormulario{
      display: none;
      position: absolute;
      z-index: 1000;
      background-color: rgba($preto, 0.4);
      left: 50px;
      right: 50px;
      top: 0;
      bottom: 200px;
      .msg{
        text-align: center;
        background-color: $vinho;
        padding: 30px;
        position: absolute;
        width: 80%;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
        border-radius: 12px;

        &:before{
          content: '';
          position: absolute;
          display: block;
          top: -5px;
          left: -5px;
          bottom: -5px;
          right: -5px;
          border-radius: 17px;
          border: solid 2px $branco;
          z-index: -1;

        }
      }
    }// resp-formulario

    .container{
      position: relative;
      @extend %myclearfix;
    }

    .map-container{
      border-radius: 22px;
      overflow: hidden;
      margin-top: 45px;
      box-shadow: 8px 8px 23px 0px rgba(0,0,0,0.65);

      @include respond-to(medium)
      {
        margin-top: 0;
      }
      #map{
        position: relative;
        width: 100%;
        display: block;
        margin: 0 auto;
        height: 360px;
      }
    }

    h1{
      margin-bottom: 20px;
    }
    .endereco{
      padding-right: 30px;
      .item{
        margin-bottom: 15px;
        span{
          display: block;
          @include respond-to(largura-site-sem-pad)
          {
            display: inline-block;
            &:not(:last-of-type):after{
              content: " - ";
              position: relative;
            }
          }
        }
      }
      p{
        font-size: 1.6em;
        line-height: 130%;

        strong{
          font-size: 1em;
        }
        /* These are technically the same, but use both */
        overflow-wrap: break-word;
        word-wrap: break-word;

        -ms-word-break: break-all;
        /* This is the dangerous one in WebKit, as it breaks things wherever */
        word-break: break-all;
        /* Instead use this non-standard one: */
        word-break: break-word;

        /* Adds a hyphen where the word breaks, if supported (No Blink) */
        -ms-hyphens: auto;
        -moz-hyphens: auto;
        -webkit-hyphens: auto;
        hyphens: auto;
      }
    }// endereco


    .formulario{

      input, textarea, button{
        position: absolute;
        width: calc( 100% - 10px );
        font-size: 1.7em;
        top: 0;
        left: 0;
        border: none;
        margin-left: 5px;
        padding: 8px;
        padding-left: 15px;
        height: 56px;
        border-radius: 12px;
        margin-top: 5px;
        background-color: rgba($branco, .5);
        z-index: 20;
        resize: none;
        outline: none;
      }
      input:first-of-type{
        margin-top: 5px;
      }

      .row:not(.semborda){
        height: 66px;
        position: relative;
        margin-bottom: 13px;
        padding: 0;
        &:before{
          content: '';
          position: absolute;
          display: block;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          border-radius: 17px;
          border: solid 2px $branco;

        }
      }

      .left{
        width: calc(50% - 10px );
        float: left;
        overflow: auto;
        @include respond-to(small)
        {
          width: 100%;
        }
      }

      .right{
        width: calc(50% - 10px );
        float: right;
        overflow: auto;
        @include respond-to(small)
        {
          width: 100%;
        }
      }

      textarea{
        height: 310px;
        overflow: auto;
      }

      button{
        background-color: $azul-noite;
        font-family: $font-quicksandbold;
        color: $branco;
        font-size: 1.7em;
        cursor: pointer;
        transition: background-color 0.3s;
        &:hover{
          background-color: $vinho;
        }
      }

      .row.msg{
        height: 320px;
      }

      .row.btn{
        width: 130px;
        margin: 0 auto;
      }

      .row{
        padding: 0;
      }

      .alerta{
          font-size: 1.5em;
          color: $preto;
          max-height: 0;
          margin-top: 20px;
          overflow: hidden;
          transition: .3s;
      }

      .alerta.open{
          max-height: 200px;
          padding: 20px;
          background-color: #b8c8d6;
          margin-bottom: 15px;
      }

      .alerta.open.warning{
          background-color: #ebb8b8;
      }



    }//formulario



    /* PLACEHOLDER -------------------------------------------------------------------------------- */
    	// apaga placeholder no focus
    	input:focus::-webkit-input-placeholder { color:transparent; }
    	input:focus:-moz-placeholder { color:transparent; }
    	textarea:focus::-webkit-input-placeholder { color:transparent; }
    	textarea:focus:-moz-placeholder { color:transparent; }

    	// formata placeholder
    	::-webkit-input-placeholder{ /* crome*/
    		font-family: $font-quicksandbold; color: tint($azul-noite, 30%); font-size: 16px;
    	}

    	:-moz-placeholder { /* Firefox 18- */
    	  font-family: $font-quicksandbold; color: $azul-noite; font-size: 18px;
    	}

    	::-moz-placeholder { /* Firefox 19+ */
    	  font-family: $font-quicksandbold; color: $azul-noite; font-size: 18px;
    	}

    	input:-ms-input-placeholder { /* IE 10+ */
    	  font-family: $font-quicksandbold; color: $azul-noite; font-size: 18px;
    	}
    	textarea:-ms-input-placeholder { /* IE 10+ */
    	  font-family: $font-quicksandbold; color: $azul-noite; font-size: 18px;
    	}


    	::-ms-input-placeholder { /* Edge */
    	  font-family: $font-quicksandbold; color: $azul-noite; font-size: 18px;
    	}

    	// placeholder browsers antigos
    	.my-placeholder{ /* placeholder para browser antigos (usa jquery)*/
    		font-family: $font-quicksandbold; color: $azul-noite; font-size: 18px;
    	}


      /* LOGO NUCLEO */
      .section-logoNucleo{
        position: absolute;
        bottom: 0;
        width: 100%;
        @include respond-to(large)
        {
          bottom: -20px;
        }
        @include respond-to(x-medium)
        {
          bottom: -35px;
        }
      }
      .section-logoNucleo .logoNucleo{
        text-align: center;
      }

      .logoNucleo{
        color: #a9b5bd;
      }

      .section-logoNucleo{
          padding: 17px;
          padding-right: 40px;
          background-color: shade($branco, 20%);
          height: 45px;
      }

      .section-logoNucleo .logoNucleo{
          svg{
            display: block;
            width: 49px;
            height: 10px;
            float: right;
          }
      }





  }//sc-contato
}//pg-contato


.rounded{
  .gm-style{
    border-radius: 22px !important;
    height: 100px;
  }
}
