.pg-selecao{
  .sc-selecao{
    text-align: right;

    padding-top: 50px;
    padding-bottom: 100px;
    position: relative;
    //@include bgFoto("../img/bgs/bg_historia.jpg", top left, 100%);
    background-color: #a2cef1;
    background-image: url(../img/bgs/marca_daqua_selecao.png), url(../img/bgs/bg_selecao.jpg);
    background-size: auto, calc(100% + 200px);
    background-position: right 30% top 65px, bottom right -200px;
    background-repeat: no-repeat;

    @include respond-to(s1500)
    {
      background-size: auto, calc(100% + 500px);
    }

    @include respond-to(h1000)
    {
      background-image: url(../img/bgs/marca_daqua_selecao.png), url(../img/bgs/bg_selecao2.jpg);
      background-size: 40%, cover;
      background-position: right 10% top 40px, bottom center;
    }

    .content{
      position: relative;
      padding-right: 75%;
      text-align: left;

      @include respond-to(hist2)
      {
        padding-right: 65%;
      }

      @include respond-to(h1000)
      {
        padding-right: 0;
      }




      h1{
        text-align: left;
      }
      h2{
        margin-top: 27px;
        margin-bottom: 27px;
      }
      p{
        font-size: 1.7em;
        margin-bottom: 30px;
      }
    }


  } //sc-historia
}
