%myclearfix {
    zoom: 1;

    &:after {
    content: ".";
    clear: both;
    display: block;
    height: 0;
    visibility: hidden;
    }

}

@mixin fotoContorno {
  padding-bottom: 22%;
  border-bottom-width: 0;
  box-shadow: 0 0 0 11px $branco,
  0 0 0 12px $preto;
  margin-left: 12px;
}


@mixin bgFoto($img, $align, $size) {
  &:before{
    content: "";
    position: absolute;
    display: block;
    top: 0;

    left: 0;
    right: 0;
    bottom: 0;
    background-image: url($img);
    background-repeat: no-repeat;
    background-position: $align;
    background-size: $size;
    background-origin: content-box;

  }
}
