$colGaleria: (
  math: fluid,
  columns: 11,
  column-width: 110px,
  gutters: 30px/110px
);


.pg-home
{
  .sc-galeria{
    @include with-layout($colGaleria)
    {
      .menu{
        @include span(5);
        @include respond-to(menu-mobile-home)
        {
          @include span(100%);
        }
      }
      .video{
        @include span(6 last);
        @include respond-to(menu-mobile-home)
        {
          @include span(100%);
        }

      }
    }
  } // sc-galeria
} // pg-home
