
.pg-touros.info{
  .sc-touros{
    padding-bottom: 20px;
    .content{
      position: relative;

      h1{
        @include respond-to(s850)
        {
          padding-top: 60px;
        }
        @include respond-to(small)
        {
          margin-bottom: 20px;
        }
      }

      .btVoltar{

        display: block;
        position: absolute;
        right: 0;
        top: 5px;
        width: 290px;

        @include respond-to(small)
        {
          right: auto;
          left: 0;
        }
        #bg{
          display: block;
          width: 290px;
          height: auto;
          fill: $vinho;
          transition: fill 0.3s;
          &:hover{
            fill: shade($vinho, 30%);
          }
        }

      }
      .info1{
        .foto{
          position: relative;
          width: calc(100% - 20px);
          display: block;
          padding-bottom: 68%;
          margin: 10px;
          border-radius: 40px;

          &:before{
            content: "";
            position: absolute;
            top: -10px;
            left: -10px;
            bottom: -10px;
            right: -10px;
            border: solid 2px $azul-noite;
            border-radius: 50px;
          } // before

          @include respond-to(small)
          {
            width: calc(100% - 100);
            margin-right: 30px;
            border-radius: 25px;
            top: 0;
            left: 0;

            padding-bottom: 55%;

            &:before{
              content: none;
            }
          }

        }// foto

        .central{
          display: block;
          margin-top: 33px;
          min-height: 70px;
          position: relative;

          .logo{
            float: left;
            vertical-align: bottom;
            img{
              position: relative;
              max-height: 70px;
              max-width: 132px;
              vertical-align: top;
            }
          }

          p{
            display: block;
            position: relative;
            float: left;
            font-size: 1.5em;
            width: calc(100% - 143px);
            color: $cinza-esc;
            display: block;
            line-height: 20px;
            margin-left: 10px;
            padding-left: 5px;
            margin-top: 4px;
            border-left: 1px solid $azul-noite;

            strong{
              font-family: $font-quicksandbold;
              font-size: 1em;
              color: inherit;

            }

          }
        } // central

      }// info1

      .info2{
        font-size: 1em;
        padding-left: 9%;

        @include respond-to(largura-site)
        {
          padding-left: 3%;
        }

        @include respond-to(large2)
        {
          padding-left: 0;
          padding-top: 30px;
        }

        .nome{
          display: block;
          color: $azul-noite;
          font-family: $font-quicksandbold;
          font-size: 4em;
          margin-bottom: 20px;
          @include respond-to(small)
          {
            font-size: 3em;
          }
        }
        .dados{
          margin-bottom: 45px;
          color: $cinza-esc;
          p, span, strong{
            color: inherit;
            font-size: 1.7em;
            line-height: 120%;
          }
          span{
            display: block;
          }
          strong{
            font-size: 1em;
          }

          @include respond-to(largura-site)
          {
           margin-top: 20px;
           span{
            display: inline-block;
            margin-right: 8px;
           }
          }

        }// dados

        .pedigree{
          margin-bottom: 45px;
          color: $cinza-esc;

          p, span, strong{
            color: inherit;
            font-size: 1.7em;
            line-height: 120%;
          }

          h2{
            font-size: 1.7em;
            color: inherit;
            margin-bottom: 15px;
          }


          span{
            display: block;
          }
          strong{
            font-size: 1em;
          }

          .item
          {
            margin-bottom: 35px;
            .a{
              font-family: $font-quicksandbold;
              display: table;
              vertical-align: middle;
              padding-right: 4%;
              margin-right: 4%;

              p{
                font-size: 1em;
                display: table-cell;
                text-align: right;
                vertical-align: middle;
              }
            }
          }// item

        }// pedigree


      }//info2

      .tit{
        @include full;
        display: block;
        text-align: center;
        width: 100%;
        height: 32px;
        border: solid 2px shade($branco, 40%);
        border-bottom: none;
        font-size: 2em;
        font-family: $font-quicksandbold;
        background: $azul-noite;
        color: white;
      }
      .table{
        clear: both;
        display: block;
        overflow-x: auto;
        border: solid 2px shade($branco, 40%);
        width: 100%;
        font-size: 1.8em;
        margin-bottom: 8px;

        &:nth-of-type(3){
          margin-top: 30px;
        }

        table{
          display: table;
          width: 100%;
          border-collapse: collapse;
          border-width: 3px;
        }

        tr{
          display: table-row;
        }

        tr:not(.tit) > th{
          background: shade($branco, 15%);
          border-right: 2px solid shade($branco, 40%);
          padding: 2px 10px;
          &:last-child{
            border-right: none;
          }
        }

        td{
          text-align: center;
          border-right: 2px solid shade($branco, 40%);
          padding: 2px 10px;
          &:last-child{
            border-right: none;
          }
        }
      }


    }// content

    .fotoVideo-container{
      margin-top: 33px;
      h2{
        display: block;
        color: $azul-noite;
        font-size: 2em;
      }

      .item{
        margin-bottom: 30px;
        p{
          min-height: 45px;
          max-height: 45px;
          overflow: hidden;
        }

      }

      .videos{
        .item{
          .foto{
            position: relative;
            &:after{
              content: '';
              display: block;
              position: absolute;
              top: 0;
              left: 0;
              right: 0;
              bottom: 0;
              background-image: url(../img/bgs/icon-play.svg);
              background-repeat: no-repeat;
              background-size: 34%;
              background-position: center;
            }//after
          }//foto
        }//item
      }

    } //fotoVideo-container

  }//sc-touros
}//pg-touros
