.pg-doadoras{
  .sc-doadoras{
    text-align: right;

    padding-top: 50px;
    padding-bottom: 100px;
    position: relative;
    //@include bgFoto("../img/bgs/bg_historia.jpg", top left, 100%);
    background-color: #13506c;
    background-image: url(../img/bgs/marca_daqua_selecao.png), url(../img/bgs/bg_doadoras.jpg);
    background-size: auto, calc(100% + 800px);
    background-position: right 20% top 65px, bottom -530px right -800px;
    background-repeat: no-repeat;

    @include respond-to(hist1)
    {
      background-size: auto, calc(100% + 1000px);
      background-position: right 20% top 65px, bottom -230px right -800px;
    }

    @include respond-to(h1000)
    {
      background-image: url(../img/bgs/marca_daqua_selecao.png), url(../img/bgs/bg_doadoras2.jpg);
      background-size: 40%, cover;
      background-position: right 10% top 40px, bottom center;
    }


    .content{
      position: relative;
      padding-right: 61%;
      text-align: justify;
      @include respond-to(h1000)
      {
        padding-right: 0;
      }

      h1{
        text-align: left;
      }
      h2{
        margin-top: 27px;
        margin-bottom: 27px;
      }
      p{
        font-size: 1.7em;
        margin-bottom: 30px;
      }
    }
  } //sc-historia

  .sc-fotos{
    position: relative;
    margin-top: 35px;
    padding-bottom: 100px;

    .row.vd{
      margin-top: 35px;
    }

    .row{
      position: relative;
    }

    p.ampliar{
      position: absolute;
      right: 40px;
      top: 9px;
      color: $azul-noite;
      @include respond-to(menu-mobile-home)
      {
        display: none;
      }
    }

    h1{
      color: $azul-noite;
      text-align: center;
    }
    .fotos, .videos{
      padding-bottom: 53px;
      border-bottom: 2px solid $azul-noite;
      position: relative;
    }

    /* the slides */
    .slick-slide {
        margin: 0 10px;
    }
    /* the parent */
    .slick-list {
        margin: 0 -10px;
        @include respond-to(small)
        {
          margin: 0 10px;
        }
    }

    .doadoras-fotos-prev, .doadoras-fotos-next,
    .doadoras-videos-prev, .doadoras-videos-next{
      cursor: pointer;
      fill: shade($branco, 30%);
      width: 40px;
      transform: translateY(-50%);
      transition: fill 0.3s;
      &:hover{
        fill: $vinho;
      }
    }

    .doadoras-fotos-prev, .doadoras-videos-prev{
      position: absolute;
      left: -50px;
      top: 40%;
      @include respond-to(h1000)
      {
        left: -35px;
      }
      @include respond-to(small)
      {
        left: -17px;
        top: 44%;
      }
    }

    .doadoras-fotos-next, .doadoras-videos-next{
      position: absolute;
      right: -50px;
      top: 40%;
      @include respond-to(h1000)
      {
        right: -35px;
      }
      @include respond-to(small)
      {
        right: -17px;
        top: 44%;
      }
    }

    .item{
      .foto{
        position: relative;
        width: calc(100% - 20);
        display: block;
        padding-bottom: 62%;
        margin: 5px;
        border-radius: 20px;

        //margin-right: 30px;
        &:before{
          content: "";
          position: absolute;
          top: -5px;
          left: -5px;
          bottom: -5px;
          right: -5px;
          border: solid 2px $azul-noite;
          border-radius: 25px;
          transition: border 0.3s, background-color 0.3s;
        } // before

        &:hover:before{
          border: solid 2px $vinho;
          background-color: rgba($branco, .2);
        }
      }// foto

      &:not(:nth-child(n+4)){
        .foto{
          //margin-right: 25px;
        }
      }

      p{
        display: block;
        color: $azul-noite;
        font-family: $font-quicksandbold;
        text-align: center;
      }

      .nome{
        display: block;
        color: $azul-noite;
        font-family: $font-quicksandbold;
        font-size: 1.8em;
        margin-bottom: 15px;

        @include respond-to(small)
        {
          font-size: 3em;
        }

      }//nome

    } // item

    .videos{
      .item{
        .foto{
          position: relative;
          &:after{
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-image: url(../img/bgs/icon-play.svg);
            background-repeat: no-repeat;
            background-size: 34%;
            background-position: center;
          }//after
        }//foto
      }//item
    }

  }// sc-fotos

}
