// menu mobile ////////////////////////////////////////////////////////////////
@include respond-to(menu-mobile-home)
{
  .row.mnu{
    padding-left: 0;
    padding-right: 0;
    max-width: none;
    width: 100%;
  }

  .menu-container
  {
    z-index: 10000;
    font-family: $font-quicksandbold;
    font-size: 1.5em;
    position: relative;
    margin-left: -30px;
    margin-right: -30px;
    margin-top: -2px;

    @include respond-to(small)
    {
      margin-right: -15px;
      top: 20px;
    }

    .menu{
      width: 100%;
      background-color: $vinho;
      display: none;
      padding-top: 0;
      padding-bottom: 50px;

      &.showMenu{
        display: block;
      }
    }

    li{
      display: block;
      padding-left: 50px;
      padding-right: 50px;



      &:first-of-type{
        a{
          border-top: none;
        }
      }

      &:last-of-type{
        a{
          border-bottom: 1px solid $vinho-claro;
        }
      }


      transition: background-color 0.3s;
      a{
        border-top: 1px solid $vinho-claro;
        padding-left: 20px;
        line-height: 45px;
        display: block;
        color: $branco;
        text-align: left;

        transition: color 0.3s;

        &:hover{
          background-color: rgba($preto, .1);
        }

        &.active{
          color: $vinho-claro;
        }

      }
    }

    #icon-mobile{
      position: absolute;
      top: -100px;
      right: 50px;
      display: block;
      fill: $branco;
      width: 35px;
      height: 35px;
      cursor: pointer;
      transition: none;
      text-align: left;

      @include respond-to(small)
      {
        right: 35px;
        top: -60px;
      }


      svg{
        display: block;
        width: 35px;
        height: 35px;
      }

      @include respond-to(x-small)
      {
        top: -49px;
        width: 25px;
        height: 25px;
        svg{
          width: 25px;
          height: 25px;
        }
      }


    } // icon-mobile

  } // menu container
} // menu mobile



@include respond-to(menu-mobile-home-min)
{
  .menu-container
  {
    font-family: $font-quicksandbold;
    line-height: normal;
    z-index: 101;
    font-size: 1.5em;
    color: $branco;
    position: relative;
    @include respond-to(largura-site-sem-pad)
    {
      padding-left: 20px;
    }
    .menu{
      width: 100%;
      opacity: 0;
      position: relative;
      text-align: right;


      animation-name: fademenu;
      animation-delay: 0.3s;
      animation-play-state: running;
      animation-fill-mode: forwards;

      & > li{
        text-align: left;
        position: relative;
        display: inline-block;
        text-transform: uppercase;
        vertical-align: top;
        //font-size: 16px;

        &:not(:last-of-type):after{
          content: "|";
          position: absolute;
          top: 7px;
          right: -4px;
          display: block;
          font-family: $font-quicksandbook;
          font-size: 15px;
        }

        &:last-of-type{
          a{
            padding-right: 0;
          }
        }

        &:hover{
          //text-decoration: underline;
          ul{
            visibility: visible;
            opacity: 1;

          }
        }


        a{
          padding: 8px 8px;
          display: block;

          transition: color 0.3s;

          &:hover{
            font-family: $font-quicksandbold;
            color: $vinho-claro;
          }

          &.active{
            color: $vinho-claro;
          }

        }

        &:last-of-type{
          background-image: none;
        }

      }// li

    }

    #icon-mobile{
      display: none;
      fill: $branco;
      width: 35px;
      height: 35px;
      cursor: pointer;
      margin-top: 40px;
    } // icon-mobile

  }// menu container
} // menu desktop


@keyframes fademenu {
  0% {
    opacity: 1;
  }

  100%{
    opacity: 1;
  }

}
