$colTourosInfo: (
  math: fluid,
  columns: 12,
  column-width: 260px,
  gutters: 120px/260px
);


.pg-touros.info{
  .sc-touros{
    h1{
      color: $azul-noite;
    }
    .info1{
      width: 60%;
      float: left;

      @include respond-to(large2)
      {
        width: 100%;
      }
    } // info1

    .info2{
      width: 40%;
      float: right;

      @include respond-to(large2)
      {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
      }

      .pedigree{
        .item{
          position: relative;
          width: 100%;

          @include respond-to(largura-site)
          {
            width: 50%;
            float: left;
          }

          @include respond-to(small)
          {
            width: 100%;
            float: none;
          }


          .a{
            width: 37%;
            height: 100px;
            border-right: 1px solid $cinza-esc;
          }

          .a1, .a2{
            position: absolute;
            top: 0;
            left: 0;
            margin-left: 40%;
          }

          .a2{
            top: auto;
            bottom: 0;
            left: auto;
          }
        }
      } // pedigree


    } // info2

    // videos e fotos
    .fotoVideo-container{

      @include with-layout($colTourosInfo)
      {

      @extend %myclearfix;

        h2{
          @include span(6);
          @include respond-to(x-medium)
          {
            @include full;
            float: none;
            margin-bottom: 20px;
          }
        }
        .fotos{
          @include span(6);

          .item{
            @include gallery(3 of 6);
            @include respond-to(x-medium)
            {
              @include gallery(6 of 6);
              float: none;
            }
          }

          &.alone{
            @include span(12);
            .item{
              @include gallery(3);
              @include respond-to(medium)
              {
                @include gallery(6);
              }
              @include respond-to(x-small)
              {
                @include gallery(12);
                float: none;
              }
            }
          }//alone

        }
        .videos{
          @include span(6 last);
          .item{
            @include gallery(3 of 6);
            @include respond-to(x-medium)
            {
              @include gallery(6 of 6);
              float: none;
            }
          }

          &.alone{
            @include span(12);
            .item{
              @include gallery(3);
              @include respond-to(medium)
              {
                @include gallery(6);
              }
              @include respond-to(x-small)
              {
                @include gallery(12);
                float: none;
              }
            }
          }//alone
        }
      }// with-layout(colTourosInfo
    }



  }
}
