.pg-touros{
  .sc-touros{
    padding-top: 50px;
    position: relative;

    .content{
      h1{
        color: $azul-noite;
      }
      .touros{
        position: relative;
      }
      .item{
        margin-bottom: 96px;
        .foto{
          position: relative;
          width: calc(100% - 20);
          display: block;
          padding-bottom: 68%;
          margin: 5px;
          border-radius: 20px;

          &:before{
            content: "";
            position: absolute;
            top: -5px;
            left: -5px;
            bottom: -5px;
            right: -5px;
            border: solid 2px $azul-noite;
            border-radius: 25px;
            transition: border 0.3s, background-color 0.3s;
          } // before

          &:hover:before{
            border: solid 2px $vinho;
            background-color: rgba($branco, .2);
          }
        }// foto

        p{
          display: block;
          color: $azul-noite;
          font-family: $font-quicksandbold;
          text-align: center;
        }

        .nome{
          display: block;
          color: $azul-noite;
          font-family: $font-quicksandbold;
          font-size: 1.8em;
          margin-bottom: 15px;

        }//nome

      } // item
    }


  }//sc-touros
}//pg-touros
