* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body{
	width: 100%;
  display: block;
}

body {
  background-color: $branco;
  position: relative;
  //min-height: 100%;
  font-family: $font-quicksandbook;
  font-size: 10px;
  line-height: normal;
  color: #000;
  text-rendering: optimizeLegibility;
  line-height: normal;
}

img{
  max-width: 100%;
}

a{
	text-decoration: none;
	transition: color, .2s;

	&:visited,
	&:link{
    color: inherit;
	}

	&:active,
	&:hover{
    color: inherit;
	}

}

a:focus {
  outline: 0;
}

h1{
  font-family: $font-quicksandbold;
  margin-bottom: 40px;
	font-size: 4em;
  color: $branco;
  @include respond-to(small)
  {
    font-size: 3em ;
  }
}

h2{
	font-size: 1.7em;
  font-family: $font-quicksandbold;
  color: $branco;
}

h3{
  font-size: 3em;
  color: $preto;
}
p{
  font-size: 1.5em;
  color: $branco;
  line-height: 150%;
}

strong{
  font-family: $font-quicksandbold;
}

// $mtop: calc((100% / 9) * -1);
// $mtop2: calc((100% / 9) * -2);
// $mtop3: calc((100% / 9) * -3);
// $mtop-pos: calc((100% / 9) * 1);
// $mtop-pos2: calc((100% / 9) * 1.5);
// $mtop3-pos: calc((100% / 9) * 2.6);

$mtop: calc((100% / 16.7) * -1);
$mtop2: calc((100% / 16) * -2);
$mtop3: calc((100% / 16.7) * -3);
$mtop-pos: calc((100% / 16.7) * 1);
$mtop-pos2: calc((100% / 16.7) * 1.5);
$mtop3-pos: calc((100% / 16.7) * 3);

// SVG Icons
.hide{
	display: none;
}

// .fancybox-lock body {
//     overflow: visible !important;
// }

.fancybox-lock .fancybox-lock body .fancybox-overlay {
    overflow-y: hidden !important;
}
//$distTopo: calc(100% / 8);

.avoid-clicks {
  pointer-events: none !important;
}


a.back-to-top {
	display: none;
	width: 60px;
	height: 60px;
	text-indent: -9999px;
	position: fixed;
	z-index: 999;
	right: 14px;
	bottom: 14px;
	background-image: url(../img/btBack.png);
  background-size: 60px;

  @include respond-to(medium)
  {
    width: 35px;
    height: 35px;
    background-size: 35px;
  }
}

#swipebox-close{
  top: 20px;
  right: 30px;
}

input:-webkit-autofill {
  background-color: #000000 !important;
  background-image: none !important;
  color: red;
}


//stick footer -----------------------------------------------------------------
html, body {
  height: 100%;
}
.page-wrap {
  min-height: 100%;
  /* equal to footer height */
  margin-bottom: -200px;

  @include respond-to(menu-mobile-home)
  {
    height: auto;
    min-height: auto;
    margin-bottom: 0;
  }
}
.page-wrap:after {
  content: "";
  display: block;
}
.sc-footer, .page-wrap:after {
  height: 200px;
  @include respond-to(menu-mobile-home)
  {
    height: auto;
  }

}
.sc-footer {
  background: orange;
}

.zeroopc{
  opacity: 0;
}

