.pg-galeria{
  .sc-galeria{
    padding-top: 50px;
    padding-bottom: 50px;
    position: relative;

    .row{
      position: relative;
    }

    p.ampliar{
      position: absolute;
      right: 40px;
      top: 9px;
      color: $azul-noite;
      @include respond-to(menu-mobile-home)
      {
        display: none;
      }
    }
    h1{
      color: $azul-noite;
    }
    /* the slides */
    .slick-slide {
      margin: 0 25px;
    }
    /* the parent */
    .slick-list {
      margin: 0 -25px;
    }

    .galeria-fotos-prev, .galeria-fotos-next{
      cursor: pointer;
      fill: shade($branco, 30%);
      width: 40px;
      transition: fill 0.3s;
      &:hover{
        fill: $vinho;
      }
    }

    .galeria-fotos-prev, .galeria-videos-prev{
      position: absolute;
      left: -50px;
      top: 36%;
    }

    .galeria-fotos-next, .galeria-videos-next{
      position: absolute;
      right: -70px;
      top: 36%;
    }

    a.item{
      outline: none;
    }

    .item{
      position: relative;
      display: block;
      margin: 7px;
      border-radius: 14px;
      padding-bottom: 66%;
      width: 100%;
      margin-bottom: 50px;
      margin-right: 13px;
      margin-left: 13px;

      &:before{
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        transition: background-color 0.3s;
      }

      &:hover:before{
        background-color: rgba($branco, .2);
      }


      @include respond-to(small)
      {
        margin-left: 9px;
        margin-right: 9px;
        width: calc(100% - 18px);
      }
    }


    .bx-wrapper .bx-viewport {
        @extend %slide-reset;
        max-width: none !important;
    }

    .bx-slider-galeria-row{
      position: relative;
    }

    .bx-next, .bx-prev{
      position: absolute;
      top: 0;
      left: 0;
      width: 58px !important;
      height: 103px !important;
      display: block;
    }

    #slider-galeria-next {
      opacity: 1;
      position: absolute;
      right: -65px;
      display: block;
      width: 58px;
      height: 103px;

      transform: translateY(-50%);
      top: 50%;

      fill: $branco;
      transition: fill 0.3s !important;

      //background: url(../imagens/img/bgs/seta-banner-next.png) no-repeat; // falback
      transition: opacity 0.3s, background, 0s;

      &:hover{
        fill: $vinho;
        cursor: pointer;
      }

      @include respond-to(medium)
      {
        width: 40px;
        height: 71px;
        right: -50px;

        svg{
          width: 40px;
          height: 71px;
        }
      }

      @include respond-to(small)
      {
        right: -40px;
      }

      @include respond-to(x-small)
      {
        width: 25px;
        height: 44px;
        right: -35px;

        svg{
          width: 25px;
          height: 44px;
        }
      }

    }

    #slider-galeria-prev {
      opacity: 1;
      position: absolute;
      left: -85px;
      display: block;
      width: 58px;
      height: 103px;
      width: 58px;
      height: 103px;

      transform: translateY(-50%);
      top: 50%;

      fill: $branco;
      transition: fill 0.3s !important;
      //background: url(../imagens/img/bgs/seta-banner-prev.png) no-repeat; // falback
      transition: opacity 0.3s, background, 0s;

      &:hover{
        fill: $vinho;
        cursor: pointer;
      }

      @include respond-to(medium)
      {
        width: 40px;
        height: 71px;
        left: -50px;

        svg{
          width: 40px;
          height: 71px;
        }
      }

      @include respond-to(small)
      {
        left: -40px;
      }

      @include respond-to(x-small)
      {
        width: 25px;
        height: 44px;
        left: -35px;

        svg{
          width: 25px;
          height: 44px;
        }
      }

    }
  }// sc banner galeria
}//pg-galeria
