$colTouros: (
  math: fluid,
  columns: 12,
  column-width: 260px,
  gutters: 100px/260px
);


.pg-touros{
  .sc-touros{
    .content{
      .touros{
        @include with-layout($colTouros)
        {
          .item{
            @include gallery(3);

            //traco separador -----------------------
            &:nth-child(4n){
              &:after{
                content: '';
                display: block;
                position: absolute;
                padding-top: 48px;
                left: 0;
                right: 0;
                border-bottom: 2px solid $azul-noite;

              }
            } //traco separador ------------------------

            @include respond-to(large)
            {
              @include gallery(6);
              &:nth-child(2n){
                &:after{
                  content: '';
                  display: block;
                  position: absolute;
                  padding-top: 48px;
                  left: 0;
                  right: 0;
                  border-bottom: 2px solid $azul-noite;

                }
              } //traco separador ------------------------
            } //respond-to(large)

            @include respond-to(x-small)
            {
              @include gallery(12);
              float: none;
              &:nth-child(1n){
                &:after{
                  content: '';
                  display: block;
                  position: absolute;
                  padding-top: 48px;
                  left: 0;
                  right: 0;
                  border-bottom: 2px solid $azul-noite;

                }
              } //traco separador ------------------------
            } //respond-to(large)

            &:last-child{
              &:after{
                content: none;
              }
            }
          }

        }
      }
    }

  }
}
