.sc-footer{
  position: relative;
  width: 100%;
  background-color: $azul-noite;
  border-top: 21px solid $vinho;

  @include respond-to(x-small)
  {
    border-top: 12px solid $vinho;
  }

  //forma em cima do logo
    .aba{
        &:after{
        content: '';
        width: 323px;
        height: 37px;
        display: block;
        position: absolute;
        top: -37px;
        right: 53px;
        background-color: $azul-noite;
        transform: perspective(37px) rotateX(13deg);
      }

      @include respond-to(x-small)
      {
        display: none;
      }
    }//aba

  .row{
    position: relative;
    border: solid 1px transparent;
  }

  p{
    font-size: 1.3em;
    margin-top: 7px;
    @include respond-to(menu-mobile-home)
    {
      text-align: center;
    }
  }

  .footer-logo{
    position: absolute;
    right: 104px;
    top: 0;
    z-index: 2;

    svg{
      width: 220px;
      height: 92px;
    }

    @include respond-to(x-small)
    {
      position: relative;
      display: block;
      width: 180px;
      right: auto;
      top: auto;
      margin: 0 auto;
      margin-top: 20px;
      svg{
        width: 180px;
        height: 75px;
        margin-left: -5px;
      }
    }


  }

  .footer-menu{
    margin-top: 44px;
    padding-right: 320px;

    @include respond-to(menu-mobile-home)
    {
      text-align: center;
      margin-top: 120px;
      padding-right: 0;
    }

    @include respond-to(x-small)
    {
      margin-top: 20px;
    }

    li{
      font-family: $font-quicksandbold;

      color: $branco;
      text-align: left;
      position: relative;
      display: inline-block;
      text-transform: uppercase;
      vertical-align: top;
      font-size: 15px;

      &:first-of-type{
        margin-left: -8px;
      }
      &:not(:last-of-type):after{
        content: "|";
        position: absolute;
        top: 7px;
        right: -4px;
        display: block;
        font-family: $font-quicksandbook;
        font-size: 15px;
      }

      a{
        padding: 8px 8px;
        display: block;

        transition: color 0.3s;

        &:hover{
          font-family: $font-quicksandbold;
          color: $vinho-claro;
        }

        &.active{
          color: $vinho-claro;
        }

      }

    }

  }//

  /* LOGO NUCLEO */
  .section-logoNucleo{
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 17px;
    padding-right: 40px;
    background-color: shade($branco, 20%);
    height: 45px;

    @include respond-to(ipad)
    {
      position: relative;
      margin-top: 20px;
    }

    @include respond-to(large)
    {
      bottom: -20px;
    }
    @include respond-to(x-medium)
    {
      bottom: -35px;
    }
  }
  .section-logoNucleo .logoNucleo{
    text-align: center;
  }

  .logoNucleo{
    color: #a9b5bd;
  }

  .section-logoNucleo .logoNucleo{
      svg{
        display: block;
        width: 49px;
        height: 10px;
        float: right;
      }
  }
} // sc-footer
